export const HELLO_MESSAGE = 'hello-message';
export const TYPIST_SKILLS = 'typist-skills';
export const FULL_NAME = 'full-name';
export const ABOUT = 'about';

export const languages = ['en', 'ru'];
const languageToIndex = { en: 0, ru: 1 };

const texts = {
  [FULL_NAME]: ['Arkadii Berezkin', ' '],
  [HELLO_MESSAGE]: ['Hi, I\'m Ark and I build', ',     '],
  [TYPIST_SKILLS]: [
    [
      'landing pages.',
      'corporate websites.',
      'web apps.',
      'mobile apps.',
      'backend servers.',
      'with .',
    ],
    [
      'landing .',
      ' .',
      'web .',
      ' .',
      'backend  .',
      '   .',
    ],
  ],
  [ABOUT]: [
    [
      'TODO: implement description',
    ],
    [
      ':  ',
    ],
  ],
};

const getText = (label, language) => texts[label][languageToIndex[language]];

export default getText;



